import { moviesApi, tvApi } from 'API';
import React from 'react';
import SearchPresenter from './SearchPresenter';

// eslint-disable-next-line import/no-anonymous-default-export
export default class extends React.Component {
  state = {
    searchMovies: null,
    searchTV: null,
    searchKeyword: '',
    loading: false, //ë°ì´í„°ë¥¼ ë¶ˆëŸ¬ì˜¤ëŠ” ê²Œ ì•„ë‹ˆë¼ ì‚¬ìš©ì ì…ë ¥ í‚¤ì›Œë“œë¥¼ ê¸°ë‹¤ë¦¬ëŠ” ì¤‘
    error: null,
  };

  //Handle submit : í‚¤ì›Œë“œ ì…ë ¥
  //ì´ë²¤íŠ¸ ë°œìƒ callback func
  handleSubmit = (event) => {
    event.preventDefault();
    const { searchKeyword } = this.state; //searchKeywordë§Œ ë”°ë¡œ ì‚¬ìš©
    if (searchKeyword !== '') {
      this.searchByKeyword();
    }
  };
  //ì´ë²¤íŠ¸ ë°œìƒ callback func
  updateKeyword = (event) => {
    this.setState({
      searchKeyword: event.target.value,
    });
  };

  searchByKeyword = async () => {
    const { searchKeyword } = this.state;
    try {
      this.setState({ loading: true });
      const {
        data: { results: searchMovies },
      } = await moviesApi.searchMovies(searchKeyword);
      const {
        data: { results: searchTV },
      } = await tvApi.searchShows(searchKeyword);
      this.setState({ searchMovies, searchTV });
    } catch {
      this.setState({
        error:
          'ë„¤íŠ¸ì›Œí¬ ì—°ê²°ì´ ë¶ˆì•ˆì •í•©ë‹ˆë‹¤. ì ì‹œ í›„ ë‹¤ì‹œ ì‹œë„í•´ì£¼ì„¸ìš”. Network Error...',
      });
    } finally {
      this.setState({ loading: false });
    }
  };

  render() {
    const {
      searchMovies,
      searchTV,
      searchKeyword,
      loading,
      error,
    } = this.state;
    return (
      <SearchPresenter
        searchMovies={searchMovies}
        searchTV={searchTV}
        searchKeyword={searchKeyword}
        error={error}
        loading={loading}
        handleSubmit={this.handleSubmit} //í•¨ìˆ˜ë¥¼ ë§¤ê°œ ë³€ìˆ˜ë¡œ ë„˜ê²¨ ì¤Œ
        updateKeyword={this.updateKeyword}
      />
    );
  }
}
