import { moviesApi, tvApi } from 'API';
import React from 'react';
import DetailPresenter from './DetailPresenter';

// eslint-disable-next-line import/no-anonymous-default-export
export default class extends React.Component {
  state = {
    result: null,
    loading: true,
    error: null,
  };

  async componentDidMount() {
    const {
      match: {
        params: { id },
      },
      history: { push },
      location: { pathname },
    } = this.props;
    //ì˜ëª»ëœ íŒŒë¼ë¯¸í„°(ìˆ«ìê°€ ì•„ë‹Œ ê°’)ê°€ ë“¤ì–´ì˜¨ ê²½ìš°, api í˜¸ì¶œ ë°©ì§€
    const parseId = parseInt(id);

    if (isNaN(parseId)) {
      return push('/');
    }

    let result = null;
    try {
      if (pathname.includes('/movie/')) {
        //ì˜í™” ìƒì„¸ ì •ë³´ ë¡œë“œ
        ({ data: result } = await moviesApi.detailMovies(parseId));
      } else if (pathname.includes('/tv/')) {
        //tv ìƒì„¸ ì •ë³´ ë¡œë“œ
        ({ data: result } = await tvApi.detailShows(parseId));
      }
    } catch {
      this.setState({
        error:
          'ì‘í’ˆ ìƒì„¸ ì •ë³´ë¥¼ ê°€ì ¸ì˜¤ì§€ ëª»í–ˆìŠµë‹ˆë‹¤. ì ì‹œ í›„ ë‹¤ì‹œ ì‹œë„í•´ì£¼ì„¸ìš”.',
      });
    } finally {
      this.setState({ loading: false, result });
    }
  }

  render() {
    const { result, loading, error } = this.state;
    return <DetailPresenter result={result} error={error} loading={loading} />;
  }
}
